import { 
  LOAD_ROOMS,
  TRIGGER_ROOM
} from '../consts/roomsReducer'
import { RESET_ROOM } from '../consts/eventsReducer'

const initialState = {
  rooms: [],
  roomIsSelected: false
}

const roomsReducer = (state = initialState, action) => {
  switch(action.type) {
    case LOAD_ROOMS:
      return { ...state, rooms: action.payload }
    
    case TRIGGER_ROOM:
      return { 
        ...state,
        roomIsSelected: !state.roomIsSelected,
        rooms: state.rooms.map(room => {
          return room.id === action.id ? { ...room, selected: !state.roomIsSelected }: room
        })
      }

    case RESET_ROOM:
      return {
        ...state,
        roomIsSelected: false
      }
      
    default:
      return state
  }
}
 
export default roomsReducer


