import React from 'react'
import { render } from 'react-dom'
import App from './App'
import { Provider } from 'react-redux'
import store from './store'
import { addLocaleData, IntlProvider } from 'react-intl'
import en from 'react-intl/locale-data/en'
import ru from 'react-intl/locale-data/ru'
import getLocalMessages from './intl/getLocalMessages'
import setInitialStorage from './db/setInitialStorage';

addLocaleData([...en, ...ru])

const locale = navigator.language ? navigator.language : 'en-US'

setInitialStorage(locale)

const localeMessages = getLocalMessages(locale, 'en-US')

render(
  <Provider store={store}>
    <IntlProvider locale={locale} messages={localeMessages}>
      <App />
    </IntlProvider>
  </Provider>,
  document.getElementById('container'))


