import React, { Component, Fragment } from 'react'
import { css } from 'glamor'
import PropTypes from 'prop-types'
import glamorous, { Div } from 'glamorous'
import disableScrolling from '../HOC/disableScrolling'

const animationStyles = () => {
  const animation = css.keyframes({
    '0%': { transform: 'rotate(0deg)' },
    '100%': { transform: 'rotate(360deg)' }
  })

  const spinnerStyles = {
    fontSize: '10px',
    borderRadius: '50%',
    margin: '0px auto',
    position: 'relative',
    width: '10em',
    height: '10em',
    textAlign: 'center',
    transform: 'translateZ(0)',
    borderTop: '1.1em solid rgba(0, 89, 255, 0.1)',
    borderRight: '1.1em solid rgba(0, 89, 255, 0.1)',
    borderBottom: '1.1em solid rgba(0, 89, 255, 0.1)',
    borderLeft: '1.1em solid #0059ff',
    '::before,::after': {
      width: '10em',
      height: '10em',
      borderRadius: '50%'
    }
  }

  return { animation: `${animation} 0.9s infinite linear`, ...spinnerStyles }
}

const Spinner = glamorous.div(animationStyles)

class Loading extends Component {
  static propTypes = {
    pastDelay: PropTypes.bool
  }

  wrapperStyles = {
    position: 'absolute',
    zIndex: '1000',
    width: '100%',
    height: '100vh',
    backgroundColor: 'rgba(140, 152, 165, 0.5)',
    display: 'flex',
    alignItems: 'center'
  }

  render () {
    return (
      <Fragment>
        <Div css={this.wrapperStyles}>
          <Spinner />
        </Div>
      </Fragment>
    )
  }
}

export default disableScrolling(Loading)


