import { LOAD_USERS } from '../consts/usersReducer'

const initialState = {
  users: []
}

const dropdownReducer = (state = initialState, action) => {
  switch(action.type) {
    case LOAD_USERS:
      return { ...state, users: action.payload }
    default: 
      return state
  }
}

export default dropdownReducer


