import { 
  NAME_ERROR,
  PARTICIPANTS_ERROR,
  ROOM_ERROR,
  CAPACITY_ERROR,
  TIME_ERROR,
  SHOW_ERRORS,
  LIMIT_ERROR
} from '../consts/errorReducer'

const initialState = {
  isAllErrorsShown: false,
  nameError: true,
  partiipantsError: true,
  roomError: true,
  capacityError: false,
  timeError: false,
  limitError: false
}

const errorReducer = (state = initialState, action) => {
  switch(action.type) {
    case NAME_ERROR:
      return { ...state, nameError: action.isError }

    case PARTICIPANTS_ERROR:
      return { ...state, partiipantsError: action.isError }

    case ROOM_ERROR:
      return { ...state, roomError: action.isError }

    case CAPACITY_ERROR:
      return { ...state, capacityError: action.isError }

    case TIME_ERROR:
      return { ...state, timeError: action.isError }
    
    case LIMIT_ERROR:
      return { ...state, limitError: action.isError }

    case SHOW_ERRORS:
      return { ...state, isAllErrorsShown: !state.isAllErrorsShown }
      
    default:
      return state
  }
}

export default errorReducer


