import messages from './messages'

const flattenMessages = (messages, prefix = '') => {
  let flatMessages = {}

  Object.keys(messages).forEach(key => {
    const flatKey = prefix ? `${prefix}.${key}` : key
    const value = messages[key]

    if(typeof value === 'string') {
      flatMessages[flatKey] = value
    }

    else {
      flatMessages = {...flatMessages, ...flattenMessages(value, flatKey)}
    }
  })

  return flatMessages
}

const getLocalMessages = (userLocale, fallbackLocale) => {
  const localeSupported = Object.keys(messages).indexOf(userLocale) !== -1

  if(localeSupported) {
    return flattenMessages(messages[userLocale])
  }

  return flattenMessages(messages[fallbackLocale])
}

export default getLocalMessages


