import React, { Component } from 'react'
import { connect } from 'react-redux'
import triggerModal from '../action-creators/pageReducer/triggerModal'
import { TRIGGER_CREATE_MODAL } from '../consts/pageReducer'
import { Header, Img } from 'glamorous'
import logo from '../images/logo.png'
import SubmitBtn from './ui/SubmitBtn'
import PropTypes from 'prop-types' 
import { injectIntl, intlShape } from 'react-intl'

class HeaderComponent extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    createEventModalOpen: PropTypes.bool.isRequired,
    editEventModalOpen: PropTypes.bool.isRequired,
    triggerModal: PropTypes.func.isRequired,
  }

  headerStyles = {
    width: '100%',
    height: '70px',
    borderBottom: '2px solid #f8f9fa',
    padding: '0px',
    display:'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    '@media(max-width: 540px)': {
      borderBottom: '3px solid #f8f9fa'
    }
  }

  logoStyles = {
    marginLeft: '27px'
  }

  btnStyles = {
    marginRight: '27px'
  }

  submitBtnClick = () => {
    this.props.triggerModal(TRIGGER_CREATE_MODAL)
  }

  render () {
    const { 
      createEventModalOpen,
      editEventModalOpen,
      intl: { formatMessage },
      breakpoint
    } = this.props
    const logoAlt = formatMessage({ id: 'header.logoAlt' })
    const submitBtnText = formatMessage({ id: 'header.submitBtnText' })
    const hasSubmitBtn = !editEventModalOpen && !createEventModalOpen
    
    return (
      <Header css={this.headerStyles}>
        <Img 
          src={logo} 
          alt={logoAlt} 
          css={this.logoStyles}
        />
        {hasSubmitBtn &&
          <SubmitBtn
            text={submitBtnText}
            isRound={window.innerWidth <= breakpoint}
            onClick={this.submitBtnClick}
            css={this.btnStyles}
          />
        }
      </Header>
    )
  }
}

const mapStateToProps = state => ({
  editEventModalOpen: state.pageReducer.editEventModalOpen,
  createEventModalOpen: state.pageReducer.createEventModalOpen,
  breakpoint: state.pageReducer.mobileBreakpoint
})

const mapDispatchToProps = {
  triggerModal
}

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(HeaderComponent))


