import { LOAD_EVENTS,
  ADD_USER,
  REMOVE_USER,
  CHANGE_EVENT_NAME,
  ADD_EVENT,
  EDIT_EVENT,
  DELETE_EVENT,
  SET_EVENT_FOR_EDITING,
  UPDATE_STARTING_HOURS,
  UPDATE_STARTING_MINUTES,
  UPDATE_ENDING_HOURS,
  UPDATE_ENDING_MINUTES,
  UPDATE_DATE,
  SET_ROOM,
  RESET_ROOM,
  RESET_VALUES
} from '../consts/eventsReducer'

const thisMonth = new Date().getMonth()
const thisDate = new Date().getDate()

const defaultCurrentEvent = {
  name: '',
  users: [],
  room: {},
  date: {
    startingDate: thisDate,
    startingMonth: thisMonth,
  },
  time: {
    startingHours: '08',
    endingHours: '22',
    startingMinutes: '00',
    endingMinutes: '00'
  }
}

const initialState = {
  currentEvent: defaultCurrentEvent,
  events: []
}

const eventsReducer = (state = initialState, action) => {
  switch(action.type) {
    case LOAD_EVENTS:
      return { ...state, events: action.payload }

    case ADD_USER:
      return { ...state, currentEvent: { ...state.currentEvent, users: [ ...state.currentEvent.users, action.payload ] } }

    case REMOVE_USER:
      return { 
        ...state,
        currentEvent: { 
          ...state.currentEvent,
          users: state.currentEvent.users.filter(user => {
            return user ? user.id !== action.id : null
          }) 
        } 
      }

    case CHANGE_EVENT_NAME:
      return { ...state, currentEvent: { ...state.currentEvent, name: action.payload } }

    case ADD_EVENT:
      return { ...state, events: [ ...state.events,  { ...state.currentEvent, id: action.id } ] }

    case EDIT_EVENT:
      return { 
        ...state,
        events: state.events.map(event => event.id === action.updatedEvent.id ? action.updatedEvent : event)
      }
    case DELETE_EVENT:
      return { ...state, events: state.events.filter(event => event.id !== action.id) }

    case SET_EVENT_FOR_EDITING:
      return { ...state, currentEvent: action.payload }

    case UPDATE_STARTING_HOURS:
      return {
        ...state,
        currentEvent: { 
          ...state.currentEvent, 
          time: {
            ...state.currentEvent.time,
            startingHours: action.payload
          }
        } 
      }

    case UPDATE_STARTING_MINUTES:
      return {
        ...state,
        currentEvent: { 
          ...state.currentEvent,
          time: {
            ...state.currentEvent.time,
            startingMinutes: action.payload
          }
        } 
      }

    case UPDATE_ENDING_HOURS:
      return {
        ...state,
        currentEvent: { 
          ...state.currentEvent,
          time: {
            ...state.currentEvent.time,
            endingHours: action.payload
          }
        } 
      }

    case UPDATE_ENDING_MINUTES:
      return {
        ...state,
        currentEvent: { 
          ...state.currentEvent,
          time: {
            ...state.currentEvent.time,
            endingMinutes: action.payload
          }
        } 
      }

    case UPDATE_DATE:
      return { 
        ...state,
        currentEvent: { 
          ...state.currentEvent,
          date: {
            startingDate: action.date, 
            startingMonth: action.month 
          }
        } 
      }

    case SET_ROOM:
      return { ...state, currentEvent: { ...state.currentEvent, room: action.payload } }

    case RESET_ROOM:
      return { ...state, currentEvent: { ...state.currentEvent, room: {} } }

    case RESET_VALUES:
      return { ...state, currentEvent: defaultCurrentEvent }
      
    default:
      return state
  }
}

export default eventsReducer;


