import glamorous from 'glamorous';
import React, { Component } from 'react'
import FontAwesome from 'react-fontawesome'
import PropTypes from 'prop-types'
import { stylesShape } from '../../intl/shapes'

const SubmitBtnComponent = glamorous.button({
  padding: '0px 15px',
  height: '40px',
  borderRadius: '3px',
  backgroundColor: '#007dfe',
  color: '#ffffff',
  fontFamily: 'TextBook, sans-serif',
  fontWeight: 'normal',
  border: 'none',
  fontSize: '16px',
  cursor: 'pointer',
  outline: 'none',
  transition: '.15s ease-in-out',
  ':hover,:focus': {
    backgroundColor: '#0059ff'
  },
  ':disabled': {
    backgroundColor: '#e1e5e8',
    color: '#c8cbcf'
  },
  ':active': {
    backgroundColor: '#0011fd'
  },
  '@media(max-width: 540px)': {
    padding: '0px 20px'
  }
})

const SubmitBtnRound = glamorous.button({
  backgroundColor: '#007dfe',
  border: 'none',
  cursor: 'pointer',
  outline: 'none',
  width: '35px',
  height: '35px',
  borderRadius: '100%',
  textAlign: 'center',
  transition: '.15s ease-in-out',
  ':hover,:focus': {
    backgroundColor: '#0059ff'
  },
  ':disabled': {
    backgroundColor: '#e1e5e8',
    color: '#c8cbcf'
  },
  ':active': {
    backgroundColor: '#0011fd'
  }
})

export default class SubmitBtn extends Component {
  static propTypes = {
    text: PropTypes.string.isRequired,
    isRound: PropTypes.bool,
    css: stylesShape
  }

  render() {
    const { text, isRound, css, ...restProps } = this.props
    return isRound ? 
      <SubmitBtnRound css={css} {...restProps}>
        <FontAwesome 
            name="plus"
            tag="i"
            style={{
              color: '#ffffff'
            }}
          />
      </SubmitBtnRound> 
      :
      <SubmitBtnComponent css={css} {...restProps}>
        {text}
      </SubmitBtnComponent>
  }
}





