import PropTypes from 'prop-types'

export const userShape = PropTypes.shape({
  login: PropTypes.string.isRequired,
  avatarUrl: PropTypes.string.isRequired,
  homeFloor: PropTypes.number.isRequired,
})

export const usersShape = PropTypes.arrayOf(userShape)

export const roomShape = PropTypes.shape({
  name: PropTypes.string,
  floor: PropTypes.number,
  maxVisitors: PropTypes.number,
  selected: PropTypes.bool
})

export const roomsShape = PropTypes.arrayOf(roomShape)

export const dateShape = PropTypes.shape({
  startingDate: PropTypes.number.isRequired,
  startingMonth: PropTypes.number.isRequired
})

export const timeShape = PropTypes.shape({
  startingHours: PropTypes.string.isRequired,
  endingHours: PropTypes.string.isRequired,
  startingMinutes: PropTypes.string.isRequired,
  endingMinutes: PropTypes.string.isRequired
})

export const eventShape = PropTypes.shape({
  name: PropTypes.string.isRequired,
  users: PropTypes.arrayOf(userShape).isRequired,
  room: roomShape.isRequired,
  date: dateShape.isRequired,
  time: timeShape.isRequired,
  id: PropTypes.string
})

export const eventsShape = PropTypes.arrayOf(eventShape)


export const stylesShape = PropTypes.objectOf(PropTypes.oneOfType([
  PropTypes.string,
  PropTypes.object
]))


export const timeStripeArrayShape = PropTypes.arrayOf(PropTypes.number)

export const rearrangedRoomShape = PropTypes.shape({
  floor: PropTypes.number.isRequired,
  rooms: PropTypes.arrayOf(roomShape)
})

export const rearrangedRoomsShape = PropTypes.arrayOf(rearrangedRoomShape)

export const workingTimeShape = PropTypes.objectOf(PropTypes.string)


