import { 
  CHANGE_INPUT_VALUE,
  UPDATE_NOT_ADDED_USERS,
  SET_FOCUSED,
  CHANGE_INDEX,
  GET_SCROLLBAR
} from '../consts/dropdownReducer'

const initialState = {
  inputValue: '',
  highlightedIndex: 0,
  focused: false,
  activeId: 'active-option',
  notAddedUsers: [],
  scrollbar: {}
}

const dropdownReducer = (state = initialState, action) => {
  switch (action.type) {
    case CHANGE_INPUT_VALUE: 
      return { ...state, inputValue: action.payload }
      
    case UPDATE_NOT_ADDED_USERS:
      return { ...state, notAddedUsers: action.payload }

    case SET_FOCUSED:
      return { ...state, focused: action.payload }
    
    case CHANGE_INDEX:
      return { ...state, highlightedIndex: action.payload }
    
    case GET_SCROLLBAR:
      return { ...state, scrollbar: action.payload }

    default:
      return state
  }
}

export default dropdownReducer


