import React, { Component, Fragment } from 'react'
import './fonts/fonts.css'
import Loadable from 'react-loadable'
import Header from './components/Header'
import { connect } from 'react-redux'
import loadUsers from './action-creators/usersReducer/loadUsers'
import Loading from './components/ui/Loading'
import { injectIntl, intlShape } from 'react-intl'
import PropTypes from 'prop-types'

const Meeting = Loadable({
  loader: () => import('./components/pages/Meeting'),
  loading: Loading,
  delay: 300
})

const EventCreated = Loadable({
  loader: () => import('./components/EventCreated'),
  loading: Loading,
  delay: 300
})

const ShouldDeleteEvent = Loadable({
  loader: () => import('./components/ShouldDeleteEvent'),
  loading: Loading,
  delay: 300
})

const MainContent = Loadable({
  loader: () => import('./components/MainContent'),
  loading: Loading
})

class App extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    editEventModalOpen: PropTypes.bool.isRequired,
    createEventModalOpen: PropTypes.bool.isRequired,
    successModalOpen: PropTypes.bool.isRequired,
    deleteModalOpen: PropTypes.bool.isRequired,
    loadUsers: PropTypes.func.isRequired
  }

  componentDidMount() {
    const { loadUsers, intl: { formatMessage } } = this.props
    loadUsers()

    document.title = formatMessage({id: 'title'})
  }

  renderCreateMeeting = () => {
    const newMeetingTitle = this.props.intl.formatMessage({id: 'meeting.new.title'})

    EventCreated.preload()
    return <Meeting title={newMeetingTitle} />
  }

  renderEditMeeting = () => {
    const editMeetingTitle = this.props.intl.formatMessage({id: 'meeting.edit.title'})

    ShouldDeleteEvent.preload()
    return <Meeting title={editMeetingTitle} edit />
  }

  render() {
    const { 
      createEventModalOpen,
      successModalOpen,
      deleteModalOpen,
      editEventModalOpen
    } = this.props

    return (
      <Fragment>
        {successModalOpen &&
          <EventCreated />
        }

        {deleteModalOpen &&
          <ShouldDeleteEvent />
        }

        <Header />
        
        <MainContent hidden={createEventModalOpen || editEventModalOpen} />
        
        {createEventModalOpen &&
          this.renderCreateMeeting()
        }

        {editEventModalOpen &&
          this.renderEditMeeting()
        }
      </Fragment>
    )
  }
}

const mapStateToProps = state => ({
  editEventModalOpen: state.pageReducer.editEventModalOpen,
  createEventModalOpen: state.pageReducer.createEventModalOpen,
  successModalOpen: state.pageReducer.successModalOpen,
  deleteModalOpen: state.pageReducer.deleteModalOpen
})

const mapDispatchToProps = {
  loadUsers
}

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(App))


